.roadmap {
  padding: 8.33rem 0 4.74rem;
  background: #0A0A0A;

  &__title {
    margin-bottom: 6.5rem;
    text-align: center;
  }

  &__slider {
    overflow-x: auto;
  }

  &__layout {
    height: 42.5rem;
  }
}

@media screen and (max-width: 425px) {
  .roadmap {
    padding: 4rem 0 0;

    &__title {
      margin-left: 2rem;
      margin-bottom: 3.2rem;
      text-align: left;
    }
  }
}
