@import "assets/scss/variables";

.home {
  height: 100vh;
  max-height: 80rem;
  background-image: url('../../assets/images/home-bg.png');
  background-size: cover;
  background-position: center;

  &__inner {
    height: 100%;
    display: flex;
    align-items: center;
  }

  &__content {
    p {
      font-family: 'Bangers';
      font-size: 4rem;
      line-height: 3.6rem;
      margin: 0 0 2.5rem 0;
      color: $white;

      &:last-child {
        margin: 0;
      }
    }

    &-label {
      color: $green;
      font-size: 2.3rem;
      line-height: 2.8rem;
      font-weight: 500;
      font-style: italic;
    }
  }
}

@media screen and (max-width: 425px) {
  .home {
    max-height: 66.8rem;
    background-image: url('../../assets/images/home-bg-mobile.png');

    &__content {
      margin-bottom: 9rem;

      p {
        font-size: 2.66rem;
        line-height: 2.4rem;
        margin: 0 0 1.2rem 0;
      }

      &-label {
        font-size: 1.33rem;
        line-height: 1.59rem;
      }
    }
  }
}
