@import 'src/assets/scss/variables';

.button {
  padding: 1rem 4.5rem;
  font-family: 'Bangers';
  font-size: 3.33rem;
  text-align: center;
  background: $pinkDefault;
  cursor: pointer;
  user-select: none;
  color: $white;
}

@media screen and (max-width: 425px) {
  .button {
    padding: 0.5rem 2.5rem;
    font-size: 1.66rem;
    line-height: 1.66rem;
  }
}
