.partners {
  &__inner {
    padding: 10rem 0 13.33rem 0;
  }

  &__title {
    text-align: center;
  }

  &__content {
    width: 100%;
    //max-width: 79.83rem;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    margin: 8.33rem auto 0;

    & > img {
      height: 50px;
      margin-right: 10rem;

      &:last-child {
        margin-right: 0;
      }
    }
  }
}

@media screen and (max-width: 425px) {
  .partners {
    &__inner {
      padding: 4.75rem 0 5rem 0;
    }

    &__content {
      align-items: center;
      margin: 1.67em auto 0;

      & > img {
        width: 9rem;
        height: initial;
        margin-right: 0.66rem;
      }
    }
  }
}
