@import "assets/scss/variables";

.team-item {
  display: flex;
  flex-direction: row;
  width: 38.33rem;
  max-width: 38.33rem;
  margin-bottom: 3.33rem;

  &__avatar {
    width: 17.91rem;
    min-width: 17.91rem;
    height: 17.91rem;
    margin-right: 2.5rem;
    background: white;
  }

  &__title {
    color: $white;
    line-height: 3rem;
  }

  &__note {
    margin-top: 1rem !important;
    font-size: 1.83rem !important;
    line-height: 2.19rem !important;
  }
}

@media screen and (max-width: 425px) {
  .team-item {
    width: 9.33rem;
    flex-direction: column;
    margin-right: 1.33rem;

    &__avatar {
      width: 9.33rem;
      min-width: initial;
      height: 9.33rem;
      margin-right: 0;
    }

    &__content {
      margin-top: 1.33rem;
    }

    &__note {
      margin-top: 1rem !important;
      font-size: 1.33rem !important;
      line-height: 1.59rem !important;
    }
  }
}
