.team {
  &__inner {
    padding: 8.33rem 0 6.67rem 0;
  }

  &__title {
    margin-bottom: 5rem;
    text-align: center;
  }

  &__content {
    display: inline-flex;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 0 2.5rem;
  }
}

@media screen and (max-width: 425px) {
  .team {
    &__inner {
      padding: 2.25rem 0 5.5rem;
    }

    &__title {
      margin-bottom: 2.33rem;
    }

    &__content {
      width: 100%;
      max-width: 100%;
      flex-direction: row;
      flex-wrap: nowrap;
      grid-gap: initial;
      gap: initial;
      overflow: auto;
    }
  }
}
