@import "assets/scss/variables";

.first-drop {
  &__inner {
    padding-top: 16rem;
  }

  &__title {
    text-align: center;
  }

  &__sub-title {
    display: block;
    margin-top: 1.5rem;
    text-align: center;
    font-weight: 500;
    font-family: 'Roboto';
    font-size: 2.33rem;
    line-height: 2.8rem;
    font-style: italic;
    color: $green;
  }
}

@media screen and (max-width: 425px) {
  .first-drop {
    &__inner {
      padding-top: 7rem;
    }

    &__sub-title {
      margin-top: 1rem;
      font-size: 1.67rem;
      line-height: 2rem;
    }
  }
}
